import React from "react"
import { Card, Form, Input, Button, message, Tabs } from "antd"
import { JsonEditor as Editor } from "jsoneditor-react"
import "jsoneditor-react/es/editor.min.css"
import Terminal from "react-console-emulator"
import cbor from "cbor"

import {
  parseAuthenticatorData,
  parseAttestationObject,
  parseCredentials,
} from "./utils/output-parser"

import { derToPem } from "./utils/utils"

import base64url from "base64url"

import { register, login, initRegistration, verify } from "./webAuthAPI"
import {
  preformatMakeCredReq,
  publicKeyCredentialToJSON,
  preformatGetAssertReq,
  Base64Decode,
} from "./utils"

const { TabPane } = Tabs

const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 },
}
const tailLayout = {
  wrapperCol: { offset: 8, span: 16 },
}

class App extends React.Component {
  state = {}
  async componentDidMount() {
    const a = await window.PublicKeyCredential.isUserVerifyingPlatformAuthenticatorAvailable()
    if (!a) {
      message.error("Webauthnn not support")
    }
  }

  createWebauthConfig(username, displayName, challenge, userId) {
    const options = {
      publicKey: {
        challenge: base64url.toBuffer(challenge),

        rp: {
          name: "Webauthn Play",
          id: "webauthnplay.herokuapp.com",
        },

        user: {
          id: base64url.toBuffer(userId),
          name: username,
          displayName: displayName,
        },

        attestation: "direct",

        authenticatorSelection: { authenticatorAttachment: "platform" },
        pubKeyCredParams: [
          {
            type: "public-key",
            alg: -7, // "ES256" IANA COSE Algorithms registry
          },
        ],
      },
    }

    return options
  }

  createWebauthGetConfig(rawId, challenge) {
    return {
      publicKey: {
        challenge: base64url.toBuffer(challenge),
        allowCredentials: [
          {
            type: "public-key",
            id: base64url.toBuffer(rawId),
            transports: ["usb", "ble", "nfc", "internal"],
          },
        ],
      },
    }
  }

  handleRegister = async (values) => {
    const { username, name = "auto" } = values

    if (!username || !name) {
      message.error("Name or username is missing!")
      return
    }

    try {
      const data = await initRegistration({ username })

      const options = this.createWebauthConfig(
        username,
        name,
        data.challenge,
        data.userId
      )

      const createCredentialResponse = await navigator.credentials.create(
        options
      )
      const jsonBody = publicKeyCredentialToJSON(createCredentialResponse)

      await register({
        username,
        name,
        publicKeyCredential: jsonBody,
      })
    } catch (error) {
      message.error(error.message)
    }
  }

  handleLogin = async (values) => {
    let username = values.username2

    if (!username) {
      message.error("Username is missing!")
      return
    }

    try {
      const res = await login({ username })
      if (!res.rawId) {
        message.error("User Not Found")
      }

      const options = this.createWebauthGetConfig(res.rawId, res.challenge)

      const getCredentialResponse = await navigator.credentials.get(options)

      const jsonBody = publicKeyCredentialToJSON(getCredentialResponse)

      const verifyResult = await verify({
        username,
        publicKeyCredential: jsonBody,
      })

      if (verifyResult.result) {
        message.success("Verified")
      } else {
        message.error("Not Verified")
      }
    } catch (error) {
      message.error(error.message)
    }
  }
  render() {
    return (
      <div className="card-container">
        <Tabs type="card">
          <TabPane tab="Register" key="1">
            <div
              style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <Form
                onFinish={this.handleRegister}
                {...layout}
                id="form_register"
              >
                <Form.Item
                  label="User Name"
                  name="username"
                  rules={[
                    { required: true, message: "Please input your username!" },
                  ]}
                >
                  <Input></Input>
                </Form.Item>
                <Form.Item
                  label="Name"
                  name="name"
                  rules={[
                    { required: true, message: "Please input your name!" },
                  ]}
                >
                  <Input></Input>
                </Form.Item>
                <Form.Item {...tailLayout}>
                  <Button type="primary" htmlType="submit" form="form_register">
                    Register
                  </Button>
                </Form.Item>
              </Form>
            </div>
          </TabPane>
          <TabPane tab="Login" key="2">
            <div
              style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <Form onFinish={this.handleLogin} id="form_update">
                <Form.Item
                  label="User Name"
                  name="username2"
                  rules={[
                    { required: true, message: "Please input your username!" },
                  ]}
                >
                  <Input></Input>
                </Form.Item>
                <Form.Item {...tailLayout}>
                  <Button type="primary" htmlType="submit" form="form_update">
                    Login
                  </Button>
                </Form.Item>
              </Form>
            </div>
          </TabPane>
        </Tabs>
      </div>
    )
  }
}
export default App
